import React, { useState } from 'react'

import Login from './Login'
import Signin from './Signup'

import {
  StyledHeaderWrapper,
  StyledHeader,
  StyledParagraph
} from '../styles/styledFrontPage'

const FrontPage = () => {

  const [showLogin, setShowLogin] = useState(true)

  const handleClick = () => {
    setShowLogin(!showLogin)
  }

  return (
    <StyledHeaderWrapper>
      <StyledHeader>World Must Change</StyledHeader>
      <StyledParagraph>See graphical representation about population and emissions in the world.</StyledParagraph>
      {
        showLogin ? (
          <Login
            handleButtonClick={handleClick}
          />) :
          (
            <Signin
              handleButtonClick={handleClick}
            />
          )
      }

    </StyledHeaderWrapper>
  )
}

export default FrontPage