import loginService from '../services/login'
import userService from '../services/users'
import { history } from '../utills/history'

import { addNotification } from './notificationReducer'

export const newUserSignin = user => {
  return async dispatch => {

    try {
      await userService.createUser(user)
      dispatch(addNotification('user created', 'success'))
      // history.push('wmc')

    } catch (error) {
      console.log(error)
      dispatch(addNotification('something wrong', 'error'))
    }
  }
}

export const userLogin = credentials => {
  return async dispatch => {
    try {
      const response = await loginService.login(credentials)
      // console.log('auth login response', response)

      localStorage.setItem('token', response.token)
      dispatch(loginUser(response.user))
      history.push('wmc')

    } catch (error) {
      console.log('error')
      dispatch(addNotification('invalid username or password', 'error'))
    }
  }
}

export const getUserProfile = () => {
  return async dispatch => {
    const token = localStorage.getItem('token')
    if (token !== null) {
      try {
        const response = await userService.getUser(token)
        // console.log('get profile reducer', response)
        dispatch(loginUser(response))
        history.push('wmc')

      } catch (exception) {
        localStorage.removeItem('token')
      }
    }
  }
}

export const loginUser = userObj => ({
  type: 'LOGIN_USER',
  userObj
})

export const logoutUser = () => {
  localStorage.removeItem('token')
  history.push('frontpage')
  return {
    type: 'LOGOUT_USER'
  }
}

export const addFavoritesToUser = (favorite) => {
  return async dispatch => {
    const token = localStorage.getItem('token')
    if (token !== null) {
      try {
        await userService.addFavorite(favorite, token)

        dispatch({
          type: 'ADD_FAVORITE',
          data: favorite
        })
      } catch (exception) {
        console.log('exception ', exception)
        // console.log(' miksi error')
      }
    }
  }
}

const initialState = {
  loggedIn: false,
  userProfile: {}
}

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'LOGIN_USER':{
      const newUser = { ...action.userObj }
      return { loggedIn: true, userProfile: newUser }
    }
    case 'LOGOUT_USER':
      return { ...state, userProfile: {} }

    case 'ADD_FAVORITE':
      return {
        ...state,
        userProfile: {
          ...state.userProfile,
          favorites: state.userProfile.favorites.concat(action.data)
        }
      }

    default:
      return state
  }
}

export default authReducer


