
function updateObject(oldState, newValues) {
  return Object.assign({}, oldState, newValues)
}

const listPropertiesReducer = (state = initial, action) => {

  switch (action.type) {
    case 'CHANGE_LISTORDER': {
      let newOrder = { order: state.order === 'desc' ? 'asc' : 'desc' }
      return updateObject(state, newOrder)
    }

    case 'CHANGE_LISTPAGE':
      return updateObject(state, { page: action.page })


    case 'CHANGE_LISTSORT': {

      const addSort = { sort: action.sort }
      return { ...state, ...addSort }
    }

    case 'CHANGE_LISTLIMIT': {
      const addLimit = { limit: action.limit }
      return { ...state, ...addLimit }
    }

    case 'CHANGE_LISTYEAR':
      return updateObject(state, { year: action.year })

    case 'CHANGE_LISTFILTER': {
      const addFilter = { filter: action.filter }
      return updateObject(state, addFilter)
    }

    default: return state
  }
}

export default listPropertiesReducer

const initial = {
  sort: 'country',
  year: 2000,
  page: 1,
  order: 'desc',
  limit: 10,
  filter: ''
  // q: '',

}

export const changeListOrder = order => {
  return {
    type: 'CHANGE_LISTORDER',
    order,
  }
}

export const changeListPage = page => {
  return {
    type: 'CHANGE_LISTPAGE',
    page
  }
}

export const changeListSort = sort => {
  return {
    type: 'CHANGE_LISTSORT',
    sort
  }
}

export const changeListLimit = limit => {
  return {
    type: 'CHANGE_LISTLIMIT',
    limit
  }
}

export const changeListYear = year => {
  return {
    type: 'CHANGE_LISTYEAR',
    year,
  }
}

export const changeListFilter = filter => {
  return {
    type: 'CHANGE_LISTFILTER',
    filter
  }
}


