import React from 'react'

import { ResponsiveContainer, LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip, Legend } from 'recharts'

const SingleLineChart = ({ data, two, filter, compare, label }) => {
  // console.log('data ', data)

  return (
    <ResponsiveContainer width="99%" aspect={1.9}  >
      <LineChart data={data} >

        <XAxis
          type='number'
          dataKey="year"
          domain={['dataMin', 'dataMax']}
          interval='preserveEnd'
        />
        <YAxis
          tickFormatter={tick => {
            return tick.toLocaleString()
          }}
          width={99}
          interval='preserveEnd'
          label={{ value: label, angle: -90, position: 'insideLeft' }}
        />
        <CartesianGrid strokeDasharray="3 3" />
        <Tooltip formatter={value => value.toLocaleString()} />
        <Legend
          verticalAlign="top"
          height={45}
        />
        <Line name={filter} type="monotone" dataKey="value" stroke="#8884d8" dot={false} />
        {
          two && <Line name={compare} type="monotone" dataKey="compare" stroke="#82ca9d" dot={false} />
        }
      </LineChart>
    </ResponsiveContainer>
  )
}

export default SingleLineChart

