import React, { useState } from 'react'
import { connect } from 'react-redux'
import { newUserSignin } from '../../reducers/authReducer'

import { LinkButton } from '../utills/Button'

import {
  StyledInputField,
  StyledInputButton,
  SigninWrapper,
  LoginHeader,
  StyledForm
} from '../styles/styledLogin'

export const Signup = (props) => {
  const [username, setUsername] = useState('')
  const [name, setName] = useState('')
  const [password, setPassword] = useState('')

  const submit = async (event) => {
    event.preventDefault()
    props.newUserSignin({ username, name, password })
    setUsername('')
    setPassword('')
    props.handleButtonClick()
  }

  return (
    <div>
      <SigninWrapper>
        <LoginHeader className='signUp'>Sign Up</LoginHeader>
        <StyledForm onSubmit={submit}>
          <StyledInputField
            id='signup_username'
            type="text"
            placeholder="Username"
            value={username}
            onChange={e => setUsername(e.target.value)}
          />
          <StyledInputField
            id='signup_name'
            type="text"
            placeholder="Name"
            value={name}
            onChange={e => setName(e.target.value)}
          />
          <StyledInputField
            id='signup_password'
            type="password"
            value={password}
            onChange={e => setPassword(e.target.value)}
            placeholder="Password"
          />
          <StyledInputButton
            id='signup_submitbutton'
            type='submit'
            onSubmit={submit}
          >
            Or sign in
          </StyledInputButton>
        </StyledForm>
        <LinkButton
          text='Or Log In'
          handleButtonClick={props.handleButtonClick}
        />
      </SigninWrapper>
    </div>
  )
}

const mapDispatchToProps = {
  newUserSignin
}

export default connect(null, mapDispatchToProps)(Signup)