import React, { useState } from 'react'
import { connect } from 'react-redux'
import {
  changeListOrder,
  changeListPage,
  changeListSort,
  changeListLimit,
  changeListYear,
  changeListFilter
} from '../../reducers/listPropertiesReducer'

import { addFavoritesToUser } from '../../reducers/authReducer'

import { debounce } from 'lodash'
// import { useDebounce } from '../hooks'
import { Divider, Segment } from 'semantic-ui-react'

// import TopTenRadiobtns from '../TopTenRadiobtns'
import SingleSlider from '../SingleSlider'
import ListTable from './ListTable'
import ListCountryFilter from './ListCountryFilter'

const Lists = (props) => {
  const { listCountries } = props
  const [sliderValue, setSliderValue] = useState(2000)

  /*
  console.log('slider value ', sliderValue)
  console.log('limit', props.limit)
  console.log(typeof props.limit)
  console.log('page', props.page)
  console.log('year', props.year)
*/

  const debouncedSliderValue = debounce((value) => {
    setSliderValue(value)
    props.changeListYear(value)
  }, 500)
  // const change = (value) => setSliderValue(value)

  const handleSort = (clickedColumn) => {
    props.changeListSort(clickedColumn)
    props.changeListOrder()
  }


  const onChangePage = (event, data) => {
    const { activePage } = data
    if (activePage !== props.page) {
      props.changeListPage(activePage)
    }
  }

  const onChangeLimit = (event, data) => {
    if (data.value !== props.limit) {
      props.changeListLimit(data.value)
    }
  }

  const onSubmitFilter = (filter) => {
    // console.log('filter ', filter)

    if (props.filter !== filter) {
      props.changeListFilter(filter)
    }
  }
  const directionConverter = (order) => {
    if (order === 'asc') {
      return 'ascending'
    } else if (order === 'desc') {
      return 'descending'
    } else {
      return null
    }
  }

  const addToFavorites = (countryName) => {
    props.addFavoritesToUser(countryName)
  }

  return (
    <Segment>
      <SingleSlider
        value={sliderValue}
        handleChange={debouncedSliderValue}
      />
      <ListCountryFilter
        filter={props.listFilter}
        totalCount={props.listCountries.length}
        onSubmitFilter={onSubmitFilter}
      // loading={this.state.loading}
      />
      <Divider />
      <ListTable
        countries={listCountries}
        totalCount={props.totalCount}
        totalPages={Math.ceil(props.totalCount / props.limit)}
        currentPage={props.page}
        onChangePage={onChangePage}
        column={props.sort}
        direction={directionConverter(props.order)}
        handleSort={handleSort}
        onChangeLimit={onChangeLimit}
        limit={props.limit.toString()}
        addFavorite={addToFavorites}
      />
    </Segment>
  )
}

const listToShow = ({ countries, listProperties, authentication }) => {
  console.log('counries')
  console.log('lis properties', listProperties)
  console.log('authentication ', authentication)

  let listArray = []
  const indexYear = listProperties.year - 1960

  if (listProperties.filter !== '') {
    countries = countries.filter(
      c => c.name.toLowerCase().startsWith(listProperties.filter)
    )
  }

  countries.forEach((country) => {
    // console.log('country ', country)
    const newCountry = { ...country }
    newCountry.population = country.population[indexYear]
    newCountry.emissions = country.emissions[indexYear]
    newCountry.perCapita = country.perCapita[indexYear]
    newCountry.year = 1960 + indexYear
    newCountry.favorite = authentication.userProfile.favorites.includes(country.name)
    listArray.push(newCountry)
  })

  if (listProperties.sort === 'population') {
    if (listProperties.order === 'asc') {
      listArray.sort((p1, p2) => { return p1.population - p2.population })
    } else {
      listArray.sort((p1, p2) => { return p2.population - p1.population })
    }
  } else if (listProperties.sort === 'country') {
    if (listProperties.order === 'asc') {
      listArray.sort((p1, p2) => {
        if (p1.name < p2.name) {
          return 1
        }
        if (p1.name > p2.name) {
          return -1
        } else {
          return 0
        }
      })
    } else {
      listArray.sort((p1, p2) => {
        if (p1.name < p2.name) {
          return -1
        }
        if (p1.name > p2.name) {
          return 1
        } else {
          return 0
        }
      })
    }
  } else if (listProperties.sort === 'emissions') {
    if (listProperties.order === 'asc') {
      listArray.sort((e1, e2) => { return e1.emissions - e2.emissions })
    } else {
      listArray.sort((p1, p2) => { return p2.emissions - p1.emissions })
    }
  } else if (listProperties.sort === 'perCapita') {
    if (listProperties.order === 'asc') {
      listArray.sort((c1, c2) => { return c1.perCapita - c2.perCapita })
    } else {
      listArray.sort((p1, p2) => { return p2.perCapita - p1.perCapita })
    }
  } else if (listProperties.sort === 'favorite') {
    listArray = listArray.filter((country) => {
      return country.favorite
    })
  }

  const slicedListArray =
    listArray.slice(
      (listProperties.page - 1) * listProperties.limit, listProperties.page * listProperties.limit)

  return slicedListArray
}

const mapStateToProps = (state) => {
  // console.log('state', state)
  return {
    listCountries: listToShow(state),
    limit: state.listProperties.limit,
    totalCount: state.countries.length,
    sort: state.listProperties.sort,
    page: state.listProperties.page,
    year: state.listProperties.year,
    order: state.listProperties.order,
    listFilter: state.listProperties.filter
  }
}

const dispatchToProps = {
  changeListOrder,
  changeListPage,
  changeListSort,
  changeListLimit,
  changeListYear,
  changeListFilter,
  addFavoritesToUser
}

export default connect(
  mapStateToProps,
  dispatchToProps
)(Lists)




