import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import {
  Router,
  Route,
  Redirect,
  Switch
  // useLocation /* Link, withRouter */
} from 'react-router-dom'
import { history } from './utills/history'

import { getUserProfile, logoutUser } from './reducers/authReducer'

// import components
import Home from './components/Home'
import FrontPage from './components/auth/FrontPage'
import NoMatch from './components/NoMatch'
import About from './components/About'


const App = (props) => {

  useEffect(() => {
    // console.log('get user profile')
    props.getUserProfile()
  }, [])

  const handleLogout = () => {
    props.logoutUser()
  }

  console.log('render')
  return (

    <Router history={history}>
      <Switch>
        <Route path="/frontpage">
          <FrontPage />
        </Route>
        <Redirect exact from='/' to='frontpage' />
        <PrivateRoute
          path='/wmc'
          userLoggedIn={props.userProfile.loggedIn}>
          <Home handleLogout={handleLogout} />
        </PrivateRoute>
        <PrivateRoute
          path='/about'
          userLoggedIn={props.userProfile.loggedIn}>
          <About
            handleLogout={handleLogout}
            user={props.userProfile}
          />
        </PrivateRoute>
        <Route path="*" component={NoMatch} />
      </Switch>
    </Router>
  )
}

const mapStateToProps = (state) => {
  return { userProfile: state.authentication }
}

const mapDispatchToProps = {
  getUserProfile,
  logoutUser
}

export default connect(
  mapStateToProps, mapDispatchToProps
)(App)

const PrivateRoute = ({ children, userLoggedIn, ...rest }) => {
  // console.log('renderoituuko')
  // console.log('user logged in', userLoggedIn)

  return (
    <Route
      {...rest}
      render={({ location }) =>
        userLoggedIn === true ? (
          children
        ) :
          (
            <Redirect to={{
              pathname: '/frontpage',
              state: { from: location }
            }}
            />
          )
      }
    />
  )

}

