import React, { useEffect } from 'react'
import { connect } from 'react-redux'

// import { Container } from 'semantic-ui-react'
// import Hello from './HelloWorld'

//import components
import Options from './Options'
import Lists from './lists/Lists'
import Charts from './Charts'

// import styles
import StyledAppLayout from './styles/layouts/StyledAppLayout'
import StyledHeaderLayout from './styles/layouts/StyledHeaderLayout'
import StyledFooterLayout from './styles/layouts/StyledFooterLayout'
import StyledOptionsLayout from './styles/layouts/StyledOptionsLayout'
import StyledViewLayout from './styles/layouts/StyledViewLayout'
import Navbar from './NavBar'
import { initializeCountries } from '../reducers/countryReducer'



const Home = (props) => {

  useEffect(() => {
    console.log('effect')
    props.initializeCountries()
  }, [props])


  let toShow
  if (props.option === 'optionOne') {
    toShow = <Charts />
  } else if (props.option === 'optionTwo') {
    toShow = <Charts />
  } else if (props.option === 'optionThree') {
    toShow = <Lists />
  }


  return (
    <StyledAppLayout>
      <StyledHeaderLayout>
        <Navbar
          handleLogout={props.handleLogout}
          userProfile={props.user.userProfile}
          to='/about'
          navLinkText='about the site'
        />
      </StyledHeaderLayout>
      <StyledOptionsLayout>
        <Options />
      </StyledOptionsLayout>
      <StyledViewLayout >
        {toShow}
      </StyledViewLayout>
      <StyledFooterLayout>
        <p style={{ color: 'white' }}>Footer</p>
      </StyledFooterLayout>
    </StyledAppLayout>
  )
}

const mapStateToProps = (state) => {
  return {
    option: state.options,
    user: state.authentication
  }
}

const dispatchToProps = { initializeCountries }

export default connect(
  mapStateToProps, dispatchToProps
)(Home)

