import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { Form, Popup } from 'semantic-ui-react'

const regex = new RegExp('^[a-zA-Z]+$')

const ListCountryFilter = (props) => {

  const [filter, setFilter] = useState('')
  const [filterValid, setFilterValid] = useState(true)

  /*
    const handleOnChange = (event, { name, value }) => {
      if (value !== '' && !regex.test(value)) {
        this.setState({ [name]: value, filterValid: false })
      } else {
        this.setState({ [name]: value, filterValid: true })
        this.props.onSubmitFilter(value)
      }
    }
    */
  const handleOnChange = (event) => {
    const value = event.target.value
    console.log('value ', value)

    if (value !== '' && !regex.test(value)) {
      setFilter(value)
      setFilterValid(false)
    } else {
      setFilterValid(true)
      setFilter(value)
      props.onSubmitFilter(value)
    }
  }

  let popupMessage = ''
  if (filterValid) {
    popupMessage = 'Invalid character.'
  } else if (props.totalCount === 0) {
    popupMessage = 'No results found.'
  }

  return (
    <Form>
      <Form.Group>
        <Form.Field>
          <Popup
            trigger={
              <Form.Input
                placeholder="Enter the filter."
                name="filter"
                value={filter}
                error={!filterValid}
                label="Filter"
                onChange={handleOnChange}
                icon="search"
              // loading={this.props.loading}
              />
            }
            content={popupMessage}
            on="click"
            open={!filterValid || props.totalCount === 0}
            position="right center"
          />
        </Form.Field>
      </Form.Group>
    </Form>
  )
}

export default ListCountryFilter



ListCountryFilter.propTypes = {
  onSubmitFilter: PropTypes.func.isRequired,
  filter: PropTypes.string.isRequired,
  totalCount: PropTypes.number.isRequired,
}
